*{
    margin: 0;
}

.icon-left{
    position: fixed;
    top: 10px;
    left: 10px;
    
}

.icon-right{
    position: fixed;
    bottom: 10px;
    right: 10px;
}

.weather-display{
    display: block;
    height: 500px
}

.weather-display .row{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h1{
    text-align: center;
}

.weather-display .row img{
    display: flex;
    align-self: center;
    width: 320px;
}

.info{
    display: flex;
    flex-direction: column;
    align-self:center;
}

.info .row{
    display: flex;
    text-align: center;
}

.winter{
    background-color: aliceblue;
}

.summer{
    background-color: lightgoldenrodyellow;
}

.autumn{
    background-color: rgb(233, 183, 133);
}

.spring{
    background-color: rgb(222, 255, 215);
}

.weather-display.winter i {
   color: blue;
   z-index: 55!important;
}

.weather-display.summer i{
    color: orange; 
    z-index: 55!important;
}

.weather-display.autumn i{
    color: rgb(168, 61, 26); 
    z-index: 55!important;
}

.weather-display.spring i{
    color: rgb(109, 151, 12); 
    z-index: 55!important;
}

#tabs{
    margin: 40px 20px;
    padding-left: 120px;
}